.Bottom{
    /* border: 1px solid black; */
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
}

.bottom-left{
    /* border: 1px solid black; */
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
}

.left-button{
    /* border: 1px solid black; */
    width: 55%;
    height: 55%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    margin-top: 10%;
}
.left-button button{
    /* border: 1px solid black; */
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-size: 1.5em;
    cursor: pointer;
}

.bottom-right{
    /* border: 1px solid black; */
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
}

.right-button{
    /* border: 1px solid black; */
    width: 55%;
    height: 55%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    margin-top: 10%;
}

.right-button button{
    /* border: 1px solid black; */
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-size: 1.5em;
    cursor: pointer;
}

.B-start{
    /* border: 1px solid black; */
    width: 60%;
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    font-size: 1.5em;
    cursor: pointer;
    margin-left: 20%;
    border-radius: 10px;
    color:aliceblue;
    background-color: #02c412;
    box-shadow: 0px 0px 10px 0px rgba(36, 203, 49, 0.5);
    border: none;
}

.B-stop{
    /* border: 1px solid black; */
    
    font-size: 1.5em;
    cursor: pointer;
    border-radius: 10px;
    color:aliceblue;
    background-color: #c40202;
    box-shadow: 0px 0px 10px 0px rgba(203, 36, 36, 0.5);
    border: none;
}
.B-reset{
    /* border: 1px solid black; */
    font-size: 1.5em;
    cursor: pointer;
    border-radius: 10px;
    color:aliceblue;
    background-color: #0297c4;
    box-shadow: 0px 0px 10px 0px rgba(36, 183, 203, 0.5);
    border: none;
}

.B-resume{
    /* border: 1px solid black; */
    font-size: 1.5em;
    cursor: pointer;
    border-radius: 10px;
    color:aliceblue;
    background-color: #5602c4;
    box-shadow: 0px 0px 10px 0px rgba(100, 36, 203, 0.5);
    border: none;
}